<template>
  <div>
    <p>Generate Java Vo Code From Chinese</p>
    <p>
      <router-link to="/">back to the contents</router-link>
    </p>
    <br>
    <textarea v-model="source" id="source" rows="50" cols="80" placeholder="source text"></textarea>
    <textarea v-model="source_en" id="source_en" rows="50" cols="80" placeholder="source en text"></textarea>
    <textarea v-model="output" id="output" rows="50" cols="80" placeholder="output" readonly></textarea>
    <br>
    <div>
      <input value="handle" type="button" @click="handle()">
      <input value="format" type="button" @click="format()">
      <input value="format_en" type="button" @click="format_en()">
    </div>
  </div>
</template>

<script>

export default {
  name: 'GenerateJavaVoCodeFromChinese',
  data () {
    return {
      source: '',
      source_en: '',
      output: '',
      ignore_field: ['请选择', '~', 'yyyy'],
      field_default_template: '@ApiModelProperty("%a")\nprivate %t %n;',
      type_map: null,
      date_field: '日期',
      number_between_field: '范围',
      enlist: null
    }
  },
  created () {
    this.type_map = new Map()
    const m = this.type_map
    m.set('s', 'String')
    m.set('ls', 'List<String>')
    m.set('i', 'Integer')
    m.set('li', 'List<Integer>')
    m.set('l', 'Long')
    m.set('ll', 'List<Long>')
    m.set('b', 'BigDecimal')
  },
  methods: {
    format_en () {
      let temp = ''
      this.enlist = []
      const sourceArr = this.source_en.split('\n').map(e => e.trim()).filter(e => e)
      for (let i = 0; i < sourceArr.length; i++) {
        const ii = sourceArr[i]
        let tempi = ''
        for (let j = 0; j < ii.length; j++) {
          const ji = ii[j]
          if (j === 0) {
            tempi = tempi + ji.toLowerCase()
          } else if (ii[j - 1] === ' ') {
            tempi = tempi + ji.toUpperCase()
          } else if (ii[j] !== ' ') {
            tempi = tempi + ji
          }
        }
        this.enlist.push(tempi)
        if (i === sourceArr.length - 1) {
          temp = temp + tempi
        } else {
          temp = temp + tempi + '\n'
        }
      }
      this.source_en = temp
    },
    format () {
      let temp = ''
      const sourceArr = this.source.split('\n').map(e => e.trim()).filter(e => e)
      let isIgnore = false
      for (let i = 0; i < sourceArr.length; i++) {
        isIgnore = false
        const ii = sourceArr[i]
        for (let j = 0; j < this.ignore_field.length; j++) {
          const ji = this.ignore_field[j]
          if (ii.includes(ji)) {
            isIgnore = true
            break
          }
        }
        if (isIgnore) {
          continue
        }
        if (i === sourceArr.length - 1) {
          temp = temp + ii
        } else {
          temp = temp + ii + '\n'
        }
      }
      this.source = temp
    },
    handle () {
      this.format()
      this.output = ''
      const sourceArr = this.source.split('\n')
      for (let i = 0; i < sourceArr.length; i++) {
        let ii = sourceArr[i]
        let t = 's'
        if (ii.includes('ll')) {
          ii = ii.substring(0, ii.length - 2)
          t = 'll'
        } else if (ii.includes('li')) {
          ii = ii.substring(0, ii.length - 2)
          t = 'li'
        } else if (ii.includes('ls')) {
          ii = ii.substring(0, ii.length - 2)
          t = 'ls'
        } else if (ii.includes('l')) {
          ii = ii.substring(0, ii.length - 1)
          t = 'l'
        } else if (ii.includes('i')) {
          ii = ii.substring(0, ii.length - 1)
          t = 'i'
        } else if (ii.includes('s')) {
          ii = ii.substring(0, ii.length - 1)
          t = 's'
        } else if (ii.includes('b')) {
          ii = ii.substring(0, ii.length - 1)
          t = 'b'
        }
        const a = ii
        if (ii.includes(this.date_field)) {
          const aArr = [a + ' start', a + ' end']
          let enArr = []
          if (this.enlist !== null) {
            enArr = [this.enlist[i] + 'StartTime', this.enlist[i] + 'EndTime']
          }
          for (let j = 0; j < 2; j++) {
            if (this.enlist === null) {
              this.output = this.output + this.field_default_template.replaceAll('%a', aArr[j]).replaceAll('%t', this.type_map.get(t)) + '\n\n'
            } else {
              this.output = this.output + this.field_default_template.replaceAll('%a', aArr[j]).replaceAll('%t', this.type_map.get(t)).replaceAll('%n', enArr[j]) + '\n\n'
            }
          }
        } else if (ii.includes(this.number_between_field)) {
          const aArr = [a + ' max', a + ' min']
          let enArr = []
          if (this.enlist !== null) {
            enArr = [this.enlist[i] + 'Max', this.enlist[i] + 'Min']
          }
          for (let j = 0; j < 2; j++) {
            if (this.enlist === null) {
              this.output = this.output + this.field_default_template.replaceAll('%a', aArr[j]).replaceAll('%t', this.type_map.get(t)) + '\n\n'
            } else {
              this.output = this.output + this.field_default_template.replaceAll('%a', aArr[j]).replaceAll('%t', this.type_map.get(t)).replaceAll('%n', enArr[j]) + '\n\n'
            }
          }
        } else {
          if (this.enlist === null) {
            this.output = this.output + this.field_default_template.replaceAll('%a', a).replaceAll('%t', this.type_map.get(t)) + '\n\n'
          } else {
            this.output = this.output + this.field_default_template.replaceAll('%a', a).replaceAll('%t', this.type_map.get(t)).replaceAll('%n', this.enlist[i]) + '\n\n'
          }
        }
      }
      navigator.clipboard.writeText(this.output).then(() => console.log('copied'))
    }
  }
}
</script>

